.itemList strong,
.itemList a{
    color: #539bf5;
    font-size: 24px;
    margin-top: 1rem;
    text-decoration: none;
    &:hover{
        text-decoration: underline;
    }
    
}

.itemList{
    display: flex;
    flex-direction: column;
    gap: .75rem;
}
.itemList p{
    color: #999;
    font-size: .75rem;
}